import React from 'react';
import styles from './App.module.scss';
import { Switch, Route, useLocation, Redirect } from 'react-router-dom';
import { ErrorBoundary } from '../ErrorBoundary/ErrorBoundary';
import { TaskList } from '../Tasks/TaskList';
import { PageLayout } from './PageLayout';
import { IconDesigner } from '../IconDesigner/IconDesigner';
import { TextEditor } from '../TextEditor/TextEditor';
import { AnalysisEditor } from '../TextEditor/AnalysisEditor';

function NoMatch() {
  let location = useLocation();

  return <>Invalid path:  {location.pathname} </>;
}

export const AppComponent = () => {
  return (
    <div className={styles.app}>
      <ErrorBoundary>
        <Switch>
          <Route path="/" exact>
            <Redirect to="/tasks" />
          </Route>
          <Route path="/editor-analysis" exact>
            <PageLayout>
              <AnalysisEditor />
            </PageLayout>
          </Route>
          <Route path="/editor-test" exact>
            <PageLayout>
              <TextEditor />
            </PageLayout>
          </Route>
          <Route path="/home" exact>
            <PageLayout>
              Home page
              <IconDesigner />
            </PageLayout>
          </Route>
          <Route path="/tasks" exact>
            <TaskList />
          </Route>
          <Route path="*">
            <NoMatch />
          </Route>
        </Switch>
      </ErrorBoundary>
    </div>
  );
};
