import { TaskState } from '../DataModel/Task';

export const getStateLabel = (state: TaskState): string => {
  switch (state) {
    case 'created':
      return 'En attente';
    case 'in-backlog':
      return 'Dans le radar';
    case 'next':
      return 'Prochainement';
    case 'active':
      return 'En cours';
    case 'finished':
      return 'Termine';
    case 'abandoned':
      return 'Abandonne';
  }
};
