import React from 'react';
import styles from './TagComponent.module.scss';
import { getClasses } from '../../Helpers/cssHelper';

export type TagControlProps = {
  /** Text to be rendered in the tag. */
  text: string;

  /** If true, the tag is rendered with a more prominent color. */
  active?: boolean;

  /** If true, the tag is rendered gray instead of colored. */
  noTag?: boolean;

  /** If not undefined, the tag is clickable and onClick() is called when the tag is clicked. */
  onClick?: () => void;

  /** If not undefined, a cross is rendered, and "onRemove" is called when the cross is clicked. */
  onRemove?: () => void;
};

/** Raw rendering of a tag. */
export const TagControl: React.FunctionComponent<TagControlProps> = ({ text, active, noTag, onClick, onRemove }) => {
  return (
    <div
      className={getClasses(styles.tag, onClick && styles.clickable, active && styles.active, noTag && styles.noTag)}
      onClick={
        onClick
          ? (ev) => {
              ev.stopPropagation();
              onClick!();
            }
          : undefined
      }
    >
      {text}
      {onRemove && (
        <span
          className={styles.remove}
          onClick={(ev) => {
            ev.stopPropagation();
            onRemove!();
          }}
        >
          X
        </span>
      )}
    </div>
  );
};
