import { IBoussoleDataService } from './IBoussoleDataService';
import { IBoussoleDataState } from '../BoussoleDataState/IBoussoleDataState';
import { Task } from '../../DataModel/Task';
import { newGuid } from '../../Helpers/guidHelper';

export class BoussoleDataService implements IBoussoleDataService {
  private store: IBoussoleDataState;

  constructor(store: IBoussoleDataState) {
    this.store = store;
  }

  public createTask = (task: Task) => {
    this.store.updateState((tasks) => {
      const id = newGuid();
      const now = new Date();
      tasks[id] = {
        ...task,
        id,
        created: now,
        modified: now,
      };
      return { ...tasks };
    });
  };

  public updateTask = (id: string, updates: Partial<Task>) => {
    this.store.updateState((tasks) => {
      var task = tasks[id];
      if (!task) throw new Error(`No task found with id {id}`);
      tasks[id] = {
        ...task,
        ...updates,
      };
      return { ...tasks };
    });
  };
}
