export type PhrasingPart = {
  /** text of the part. May be empty. */
  text: string;
  href?: string;
  strong?: boolean;
  em?: boolean;
};

export type Block = {
  type: 'ul' | 'ol' | 'p';
  /** parts of the block. There is always at least one part in a block. May be empty only if there is a single part in the block. */
  parts: PhrasingPart[];
};

export type Position = {
  blockIndex: number;
  charIndex: number;
};

export type TextEditorSelection = {
  // element path to the start of the selection.
  start: Position;
  // element path to the end of the selection. Always equal to or after start. Same object as start (===) if there is a cursor and no selection (=> if the selection is collapsed).
  end: Position;
};

export type TextEditorState = {
  blocks: Block[];
  selection: TextEditorSelection | null;
};

