import React from 'react';
import { AllTags } from '../../DataModel/Tags';
import { TagControl } from './TagControl';

export type TagComponentProps = {
  /** ID of the tag to be rendered. null means "no tag".*/
  tagId: string | null;

  /** If true, the tag is rendered with a more prominent color. */
  active?: boolean;

  /** If defined, the tag is clickable, and onClick is called when the tag is clicked. */
  onClick?: (tagId: string | null) => void;

  /** If not undefined, a cross is rendered, and "onRemove" is called when the cross is clicked. */
  onRemove?: () => void;
};

/** displays a tag, calls onClick when the tag is clicked (if onClick is not undefined). */
export const TagComponent: React.FunctionComponent<TagComponentProps> = ({ tagId, active, onClick, onRemove }) => {
  const tag = AllTags.find((t) => t.id === tagId);
  return (
    <TagControl
      text={tag ? tag.name : 'Pas de catgorie'}
      active={active}
      noTag={tagId === null}
      onClick={onClick ? () => onClick(tagId) : undefined}
      onRemove={onRemove}
    />
  );
};
