import { IParticleState, ParticleStateListener } from './IParticleState';

export const toto = 'tutu';

export class ParticleState<TState> implements IParticleState<TState> {
  private state: TState;
  private callbacks: ParticleStateListener<TState>[];

  constructor(state: TState) {
    this.state = state;
    this.callbacks = [];
  }

  public getState = () => this.state;

  public setState = (state: TState) => {
    this.state = state;
    this.notify();
  };

  public updateState = (updateFunc: (state: TState) => TState) => {
    var newState = updateFunc(this.state);
    if (newState !== this.state) {
      this.state = newState;
      this.notify();
    }
  };

  public subscribe = (callback: ParticleStateListener<TState>) => {
    if (this.callbacks.indexOf(callback) >= 0) {
      throw new Error('The callback is already registered.');
    }

    this.callbacks.push(callback);

    return () => {
      this.callbacks = this.callbacks.filter((c) => c !== callback);
    };
  };

  private notify = () => {
    var state = this.state;
    for (var callback of this.callbacks) {
      if (this.state !== state) {
        // a callback has updated the state again. The next callbacks are not called, all callbacks will be called with the new value.
        return;
      }

      if (this.callbacks.indexOf(callback) < 0) {
        // the callback has been removed in a callback.
        continue;
      }

      callback(state);
    }
  };
}
