import React, { CSSProperties } from 'react';
import styles from '../Tasks.module.scss';
import { getClasses } from '../../../Helpers/cssHelper';
import { TaskState } from '../../../DataModel/Task';
import { getStateLabel } from '../../../Helpers/textHerlper';
import { getCssColor, TaskColors, darker } from '../../../Helpers/Colors';

export type TaskStateDisplayProps = {
  /** State to be rendered. */
  state: TaskState;
  /** Show the text. */
  showText?: boolean;
  /** Display a thick border. */
  showCurrentBorder?: boolean;
  /** Display an attenuated color. */
  showNotCurrent?: boolean;
  /** Display as a button and call onClick when clicked. If undefined, the control is not clickable. */
  onClick?: () => void;
};

const getStyle = (state: TaskState, showCurrentBorder?: boolean, showNotCurrent?: boolean): CSSProperties => {
  return {
    backgroundColor: getCssColor(TaskColors[state]),
    borderColor: getCssColor(darker(TaskColors[state], 0.2)),
  };
};

/** Shows a task state as a rounded rectangle, with a background color corresponding to the state, and optionally the state's text label. */
export const TaskStateIndicator: React.FC<TaskStateDisplayProps> = ({
  state,
  showText,
  showCurrentBorder,
  showNotCurrent,
  onClick,
}) => {
  return (
    <div
      title={showText ? undefined : getStateLabel(state)}
      className={getClasses(
        styles.taskStateIndicator,
        !showText && styles.small,
        showCurrentBorder && styles.thickBorder,
        showNotCurrent && styles.notCurrent,
        onClick && styles.clickable
      )}
      style={getStyle(state, showCurrentBorder, showNotCurrent)}
      onClick={
        onClick
          ? (event) => {
              event.stopPropagation();
              onClick();
            }
          : undefined
      }
    >
      {showText && getStateLabel(state)}
    </div>
  );
};
