import React from 'react';
import styles from './IconDesigner.module.scss';
import { IconPreview } from './IconPreview';

export const IconDesigner: React.FunctionComponent = () => {
  var [svgValue, setSvgValue] = React.useState('');

  return (
    <div className={styles.iconDesigner}>
      <textarea value={svgValue} onChange={(ev) => setSvgValue(ev.target.value)} />
      <IconPreview svg={svgValue} size={200} />
      <IconPreview svg={svgValue} size={100} />
      <IconPreview svg={svgValue} size={50} />
      <IconPreview svg={svgValue} size={32} />
      <IconPreview svg={svgValue} size={25} />
    </div>
  );
};
