import React from 'react';
import { BoussoleData } from '../../DataModel/BoussoleData';
import { useBoussoleDataState } from '../BoussoleDataState/BoussoleDataStateProvider';

const BoussoleDataContext = React.createContext<BoussoleData>(undefined as any);

export const BoussoleDataProvider: React.FC = ({ children }) => {
  var boussoleDataState = useBoussoleDataState();
  const [boussoleData, setBoussoleData] = React.useState<BoussoleData>(boussoleDataState.getState());

  React.useEffect(() => boussoleDataState.subscribe(setBoussoleData), [boussoleDataState]);

  return <BoussoleDataContext.Provider value={boussoleData}>{children}</BoussoleDataContext.Provider>;
};

export const useBoussoleData = () => {
  const boussoleData = React.useContext(BoussoleDataContext);
  if (!boussoleData) {
    throw new Error('Please define a BoussoleDataProvider upper in the component tree.');
  }
  return boussoleData;
};
