import React from 'react';
import styles from './Tasks.module.scss';
import { getClasses } from '../../Helpers/cssHelper';
import { TagControl } from '../Tags/TagControl';
import { TagEditionDialog } from '../Tags/TagEditionDialog';
import { TagComponent } from '../Tags/TagComponent';

export type TaskFilterValue = {
  text: string;
  /** tasks with that ID must be displayed. null means "tasks with no tag". undefined means "do not filter on tag". */
  tagId: string | undefined | null;
};

type TaskFilterProps = {
  visible: boolean;
  value: TaskFilterValue;
  onChange: (filter: TaskFilterValue) => void;
};

export const TaskFilter: React.FunctionComponent<TaskFilterProps> = ({ visible, value, onChange }) => {
  var textRef = React.useRef<HTMLInputElement | null>(null);
  const [tagEditFormOpen, setTagEditFormOpen] = React.useState(false);

  React.useEffect(() => {
    if (visible && textRef.current) {
      textRef.current.focus();
    }
  }, [visible]);

  return (
    <div className={getClasses(styles.tasksFilter, visible && styles.visible)}>
      <div className={styles.tasksFilterInner}>
        <input
          ref={textRef}
          className={styles.taskFilterText}
          value={value.text}
          onChange={(ev) => onChange({ ...value, text: ev.target.value })}
        />
        {value.tagId === undefined ? (
          <TagControl text="..." noTag onClick={() => setTagEditFormOpen(true)} />
        ) : (
          <TagComponent
            tagId={value.tagId}
            active
            onClick={() => setTagEditFormOpen(true)}
            onRemove={() => onChange({ ...value, tagId: undefined })}
          />
        )}
      </div>
      {tagEditFormOpen && (
        <TagEditionDialog
          tagId={value.tagId}
          onChange={(tagId) => {
            onChange({ ...value, tagId });
            setTagEditFormOpen(false);
          }}
          onClose={() => setTagEditFormOpen(false)}
        />
      )}
    </div>
  );
};
