import React from 'react';
import { IBoussoleDataStore } from './IBoussoleDataStore';
import { BoussoleDataStore } from './BoussoleDataStore';
import { useAuthManager } from '../Auth/AuthManagerProvider';

const BoussoleDataStoreContext = React.createContext<IBoussoleDataStore>(undefined as any);

export const BoussoleDataStoreProvider: React.FC = ({ children }) => {
  const authManager = useAuthManager();
  const boussoleDataStore = React.useMemo(() => new BoussoleDataStore(authManager), [authManager]);
  return <BoussoleDataStoreContext.Provider value={boussoleDataStore}>{children}</BoussoleDataStoreContext.Provider>;
};

export const useBoussoleDataStore = () => {
  const boussoleDataStore = React.useContext(BoussoleDataStoreContext);
  if (!boussoleDataStore) {
    throw new Error('Please define a BoussoleDataStoreProvider upper in the component tree.');
  }
  return boussoleDataStore;
};
