import React from 'react';
import { IBoussoleDataService } from './IBoussoleDataService';
import { BoussoleDataService } from './BoussoleDataService';
import { useBoussoleDataState } from '../BoussoleDataState/BoussoleDataStateProvider';

const BoussoleDataServiceContext = React.createContext<IBoussoleDataService>(undefined as any);

export const BoussoleDataServiceProvider: React.FC = ({ children }) => {
  const boussoleDataState = useBoussoleDataState();
  const boussoleDataService = React.useMemo(() => new BoussoleDataService(boussoleDataState), [boussoleDataState]);

  return (
    <BoussoleDataServiceContext.Provider value={boussoleDataService!}>{children}</BoussoleDataServiceContext.Provider>
  );
};

export const useBoussoleDataService = () => {
  const boussoleDataService = React.useContext(BoussoleDataServiceContext);
  if (!boussoleDataService) {
    throw new Error('Please define a BoussoleDataServiceProvider upper in the component tree.');
  }
  return boussoleDataService;
};
