import React from 'react';
import styles from './TextEditor.module.scss';

export const AnalysisEditor: React.FC = () => {
  const [events, setEvents] = React.useState('');

  var sectionRef = React.useRef<HTMLElement>(null);

  const beforeInputHandler = React.useCallback((event) => {
    const inputEvent = event as InputEvent;
    console.log('event', inputEvent.inputType, inputEvent.data, inputEvent);
    setEvents(
      (events) =>
        `beforeInput-${inputEvent.inputType} type:${inputEvent.type} ${
          inputEvent.data
        } cancelable:${inputEvent.cancelable.toString()} isComposing:${inputEvent.isComposing}\n${events}`
    );
    // if (inputEvent.inputType === 'insertText') {
    //   setState((s) => textTyped(s, inputEvent.data || ''));
    // }
    // if (inputEvent.inputType === 'deleteContentBackward') {
    //   setState(deleteContentBackward);
    // }
    // if (inputEvent.inputType === 'deleteContentForward') {
    //   setState(deleteContentForward);
    // }
    // if (inputEvent.inputType === 'insertParagraph') {
    //   setState(insertParagraph);
    // }
    // event.preventDefault();
  }, []);

  const selectionChangeEventHandler = React.useCallback(() => {
    const selection = document.getSelection();
    let desc: string;
    if (!selection) {
      desc = 'no selection';
    } else {
      if (!selection.focusNode) {
        desc = 'no focus node';
      } else {
        desc = `focus ${selection.focusNode.nodeType}:${selection.focusOffset}`;
      }

      if (selection.anchorNode) {
        desc += `, anchor ${selection.anchorNode.nodeType}:${selection.anchorOffset}`;
      }
    }
    setEvents((events) => `selectionchange ${desc}\n${events}`);
    console.log(`selectionchange ${desc}\n`, selection);
  }, []);

  const inputLogger = React.useCallback((event) => {
    const inputEvent = event as InputEvent;
    setEvents(
      (events) =>
        `input-${inputEvent.inputType} type:${inputEvent.type} ${
          inputEvent.data
        } cancelable:${inputEvent.cancelable.toString()} isComposing:${inputEvent.isComposing}\n${events}`
    );
  }, []);

  const compositionEventLogger = React.useCallback((ev) => {
    const event = ev as CompositionEvent;
    setEvents(
      (events) => `composition-${event.type} ${event.data} cancelable:${event.cancelable.toString()}\n${events}`
    );
  }, []);

  React.useLayoutEffect(() => {
    var div = sectionRef.current!;
    div.addEventListener('beforeinput', beforeInputHandler);
    document.addEventListener('selectionchange', selectionChangeEventHandler);
    div.addEventListener('input', inputLogger);
    div.addEventListener('compositionstart', compositionEventLogger);
    div.addEventListener('compositionupdate', compositionEventLogger);
    div.addEventListener('compositionend', compositionEventLogger);

    const callback: MutationCallback = (mutationsList) => {
      for (const mutation of mutationsList) {
        if (mutation.type === 'attributes') {
          console.log('mutationObserver-attributes: the ' + mutation.attributeName + ' attribute was modified.');
        } else {
          console.log(`mutationObserver-${mutation.type}`);
        }
      }
    };
    const observer = new MutationObserver(callback);
    observer.observe(div, { attributes: true, childList: true, subtree: true });

    return () => {
      div.removeEventListener('beforeinput', beforeInputHandler);
      document.removeEventListener('selectionchange', selectionChangeEventHandler);
      div.removeEventListener('input', inputLogger);
      div.removeEventListener('compositionstart', compositionEventLogger);
      div.removeEventListener('compositionupdate', compositionEventLogger);
      div.removeEventListener('compositionend', compositionEventLogger);
      observer.disconnect();
    };
  }, [beforeInputHandler, selectionChangeEventHandler, inputLogger, compositionEventLogger]);

  return (
    <div>
      <div>
        <section ref={sectionRef} contentEditable className={styles.textEditor} suppressContentEditableWarning={true}>
          <p>
            Hello, this is some text with a
            <span contentEditable={false} style={{ backgroundColor: 'yellow' }}>
              page reference
            </span>
            , <span>which</span>
            <span> is</span> not editable.
          </p>
        </section>
      </div>
      <div>
        <pre>{sectionRef.current ? sectionRef.current.outerHTML : ''}</pre>
      </div>
      <div>
        <pre>{events}</pre>
      </div>
    </div>
  );
};
