import React from 'react';
import { Dialog } from '../Utility/Dialog/Dialog';
import { TagComponent } from './TagComponent';
import { AllTags } from '../../DataModel/Tags';

export type TagEditionDialogProps = {
  tagId: string | null | undefined;
  onChange: (tagId: string | null) => void;
  onClose: () => void;
};

/** Edition of a task's state, displays all possible states. Calls onClose when a state is selected, or when "Cancel" is clicked. */
export const TagEditionDialog: React.FunctionComponent<TagEditionDialogProps> = ({ tagId, onChange, onClose }) => {
  var allTagIdsWithNull = React.useMemo<(string | null)[]>(() => {
    const allTagIds = AllTags.map((tag) => tag.id);
    if (tagId === null || tagId === undefined) {
      return [null, ...allTagIds];
    }
    return [tagId, null, ...allTagIds.filter((id) => id !== tagId)];
  }, [tagId]);

  return (
    <Dialog
      buttons={[
        {
          text: 'Annuler',
          onclick: () => {
            onClose();
          },
        },
      ]}
    >
      {allTagIdsWithNull.map((id) => (
        <TagComponent tagId={id} active={id === tagId} onClick={id === tagId ? undefined : onChange} />
      ))}
    </Dialog>
  );
};
