import React from 'react';
import { useBoussoleDataStore } from '../BoussoleDataStore/BoussoleDataStoreProvider';
import { ParticleState } from '../../Helpers/ParticleState/ParticleState';
import { BoussoleData } from '../../DataModel/BoussoleData';
import { IBoussoleDataState } from './IBoussoleDataState';

const BoussoleDataStateContext = React.createContext<IBoussoleDataState>(undefined as any);

export interface IBoussoleDataStateProviderProps {
  getNotReadyComponents: () => React.ReactElement | null;
  getReadyComponents: () => React.ReactElement | null;
}

/** Loads and manages the boussole data. Children are rendered if data is available, otherwise a "wait" message is being displayed. */
export const BoussoleDataStateProvider: React.FC<IBoussoleDataStateProviderProps> = ({
  getNotReadyComponents,
  getReadyComponents,
}) => {
  const boussoleDataStore = useBoussoleDataStore();
  const [boussoleDataState, setBoussoleDataState] = React.useState<IBoussoleDataState | undefined>(undefined);

  React.useEffect(() => {
    (async () => {
      const data = await boussoleDataStore.loadData();
      setBoussoleDataState(new ParticleState<BoussoleData>(data));
    })();
  }, [boussoleDataStore]);

  if (boussoleDataState === undefined) {
    return <>{getNotReadyComponents()}</>;
  }

  return (
    <BoussoleDataStateContext.Provider value={boussoleDataState!}>
      {getReadyComponents()}
    </BoussoleDataStateContext.Provider>
  );
};

export const useBoussoleDataState = () => {
  const boussoleDataState = React.useContext(BoussoleDataStateContext);
  if (!boussoleDataState) {
    throw new Error('Please define a BoussoleDataStateProvider upper in the component tree.');
  }
  return boussoleDataState;
};
