import React, { ReactElement, Fragment } from 'react';
import styles from './TextEditor.module.scss';
import {
  TextEditorState,
  TestTextEditorState,
  TextEditorSelection,
  PhrasingPart,
  Position,
  deleteContentBackward,
  textTyped,
  deleteContentForward,
  insertParagraph,
} from './TextEditorState';

/** Recursively count characters of a node and its sub-nodes. */
const countCharacters = (node: Node): number => {
  if (node.nodeType === Node.TEXT_NODE) {
    return node.textContent!.length;
  }

  var length = 0;

  if (node.hasChildNodes) {
    var children = node.childNodes;

    for (var i = 0; i < children.length; i++) {
      length += countCharacters(children[i]);
    }
  }

  return length;
};

/** Compute the model position corresponding to a browser selection focus or anchor position */
const computePosition = (rootElement: HTMLElement, node: Node, offset: number): Position => {
  let charIndex = offset;

  if (node === rootElement) {
    return { blockIndex: 0, charIndex: 0 };
  }

  // build a list with all nodes of the hierarchy from node (included) to rootElement (excluded)

  let nodes: Node[] = [node];
  let parentElement = node.parentElement!;
  while (true) {
    if (parentElement === rootElement) {
      break;
    }

    nodes.push(parentElement);
    parentElement = parentElement.parentElement!;
  }

  // find out the block index and remove the element(s) which are part of the block

  let blockIndex = 0;
  let firstLevelElement: Node | null;

  if (nodes[nodes.length - 1].nodeName.toLowerCase() === 'p') {
    [firstLevelElement] = nodes.splice(nodes.length - 1);
  } else {
    // ol or ul, blockIndex depends on the li within the list.

    if (nodes.length === 1) {
      [firstLevelElement] = nodes.splice(nodes.length - 1);
    } else {
      let li: Node | null;
      [li, firstLevelElement] = nodes.splice(nodes.length - 2);
      let sibling = li.previousSibling;
      while (sibling) {
        blockIndex++;
        sibling = sibling.previousSibling;
      }
    }
  }

  firstLevelElement = firstLevelElement.previousSibling;
  while (firstLevelElement) {
    if (firstLevelElement.nodeName.toLowerCase() === 'p') {
      blockIndex++;
    } else {
      if (firstLevelElement.hasChildNodes) {
        blockIndex += firstLevelElement.childNodes.length;
      }
    }
    firstLevelElement = firstLevelElement.previousSibling;
  }

  // count the charIndex within the elements which are part of the phrasing.

  for (let i = 0; i < nodes.length; i++) {
    let sibling = nodes[i].previousSibling;
    while (sibling) {
      if (sibling) {
        charIndex += countCharacters(sibling);
      }
      sibling = sibling.previousSibling;
    }
  }

  return { blockIndex, charIndex };
};

/** Compute the model selection corresponding to a browser selection */
const computeSelection = (rootElement: HTMLElement, selection: Selection | null): TextEditorSelection | null => {
  if (
    selection === null ||
    selection.anchorNode === null ||
    selection.focusNode === null ||
    !rootElement.contains(selection.anchorNode) ||
    !rootElement.contains(selection.focusNode)
  ) {
    return null;
  }

  const focus = computePosition(rootElement, selection.focusNode, selection.focusOffset);

  if (selection.anchorNode === selection.focusNode && selection.anchorOffset === selection.focusOffset) {
    return { start: focus, end: focus };
  }

  const anchor = computePosition(rootElement, selection.anchorNode, selection.anchorOffset);

  if (
    focus.blockIndex < anchor.blockIndex ||
    (focus.blockIndex === anchor.blockIndex && focus.charIndex < anchor.charIndex)
  ) {
    return { start: focus, end: anchor };
  } else {
    return { start: anchor, end: focus };
  }
};

/** Render document nodes corresponding to a model phrasing part */
const renderPart = (part: PhrasingPart, key: string | number | undefined): ReactElement => {
  let element = <Fragment key={key}>{part.text}</Fragment>;
  if (part.strong) {
    element = <strong key={key}>{element}</strong>;
  }
  if (part.em) {
    element = <em key={key}>{element}</em>;
  }

  return element;
};

/** Render document nodes corresponding to model phrasing parts */
const renderParts = (parts: PhrasingPart[]): ReactElement => {
  if (parts.length === 1 && parts[0].text.length === 0) {
    return <br />;
  }

  const elements: ReactElement[] = [];

  // group parts with the same href together. Parts with an undefined href are not grouped together.

  for (let i = 0; i < parts.length; i++) {
    const part = parts[i];
    if (part.href) {
      const subParts: ReactElement[] = [renderPart(part, i)];
      while (i + 1 < parts.length) {
        if (parts[i + 1].href !== part.href) {
          break;
        }
        subParts.push(renderPart(parts[++i], i));
      }
      elements.push(
        <a key={i} href={part.href}>
          {subParts}
        </a>
      );
    } else {
      elements.push(renderPart(part, i));
    }
  }

  return <>{elements}</>;
};

/** Render document nodes corresponding to the whole model */
const render = (state: TextEditorState): ReactElement | null => {
  const elements: ReactElement[] = [];

  for (let i = 0; i < state.blocks.length; i++) {
    const block = state.blocks[i];
    if (block.type === 'p') {
      elements.push(<p key={i}>{renderParts(block.parts)}</p>);
    } else {
      // ul or ol
      const items: ReactElement[] = [];

      for (let itemIndex = i; itemIndex < state.blocks.length; itemIndex++) {
        const currentBlock = state.blocks[itemIndex];
        if (currentBlock.type !== block.type) {
          break;
        }
        items.push(<li key={itemIndex}>{renderParts(currentBlock.parts)}</li>);
        i = itemIndex;
      }
      if (block.type === 'ul') {
        elements.push(<ul key={i}>{items}</ul>);
      } else {
        elements.push(<ol key={i}>{items}</ol>);
      }
    }
  }

  return <>{elements}</>;
};

/** Find the child node containing the character index. If the appropriate phrasing part is found, an object is returned. Otherwise the remaining index count is returned. */
const getPhrasingPartAtIndex = (parent: Node, charIndex: number): { node: Node; charIndex: number } | number => {
  var currentIndex = charIndex;
  for (var i = 0; i < parent.childNodes.length; i++) {
    var node = parent.childNodes[i];
    if (node.nodeType === Node.TEXT_NODE) {
      if (currentIndex <= node.textContent!.length) {
        return {
          node,
          charIndex: currentIndex,
        };
      }
      currentIndex -= node.textContent!.length;
    } else {
      var subPhrasingPart = getPhrasingPartAtIndex(node, currentIndex);
      if (typeof subPhrasingPart === 'object') {
        return subPhrasingPart;
      }
      currentIndex = subPhrasingPart;
    }
  }
  return currentIndex;
};

/** Find the node and character index corresponding to a model position */
const getSelectionNodeAndCharIndex = (
  rootElement: HTMLElement,
  pos: Position
): { node: Node; charIndex: number } | undefined => {
  var blockIndex = pos.blockIndex;
  var block: Node | undefined = undefined;

  // find the block corresponding to pos.blockIndex

  for (var nodeIndex = 0; nodeIndex < rootElement.childNodes.length; nodeIndex++) {
    var node = rootElement.childNodes[nodeIndex];
    var nodeName = node.nodeName.toLowerCase();
    if (nodeName === 'p') {
      if (blockIndex === 0) {
        block = node;
        break;
      }
      blockIndex--;
    } else if (nodeName === 'ul' || nodeName === 'ol') {
      for (var itemIndex = 0; itemIndex < node.childNodes.length; itemIndex++) {
        var item = node.childNodes[itemIndex];
        if (blockIndex === 0) {
          block = item;
          break;
        }
        blockIndex--;
      }
      if (block) {
        break;
      }
    } else {
      throw new Error('Unexpected block type: ' + node.nodeName);
    }
  }

  if (!block) {
    return undefined;
  }

  // find the node and index corresponding to pos.charIndex

  if (pos.charIndex === 0) {
    return { node: block, charIndex: 0 };
  }

  var phrasingPart = getPhrasingPartAtIndex(block, pos.charIndex);
  if (typeof phrasingPart === 'object') {
    return phrasingPart;
  }

  return undefined;
};

/** Set the document selection so it matches the state selection. */
const ensureStateSelection = (rootElement: HTMLElement, state: TextEditorState) => {
  let selection = window.getSelection();

  if (!selection) {
    return;
  }

  if (state.selection === null) {
    selection.removeAllRanges();
    return;
  }

  while (selection.rangeCount > 1) {
    selection.removeRange(selection.getRangeAt(1));
  }

  var editorCompatibleSelection = computeSelection(rootElement, selection);

  if (
    editorCompatibleSelection == null ||
    editorCompatibleSelection.start.blockIndex !== state.selection.start.blockIndex ||
    editorCompatibleSelection.start.charIndex !== state.selection.start.charIndex ||
    editorCompatibleSelection.end.blockIndex !== state.selection.end.blockIndex ||
    editorCompatibleSelection.end.charIndex !== state.selection.end.charIndex
  ) {
    selection.removeAllRanges();
    var startNodeAndCharIndex = getSelectionNodeAndCharIndex(rootElement, state.selection.start);
    var endNodeAndCharIndex = getSelectionNodeAndCharIndex(rootElement, state.selection.end);
    if (startNodeAndCharIndex && endNodeAndCharIndex) {
      let range = document.createRange();
      range.setStart(startNodeAndCharIndex.node, startNodeAndCharIndex.charIndex);
      range.setEnd(endNodeAndCharIndex.node, endNodeAndCharIndex.charIndex);
      selection.addRange(range);
    }
  }
};

/** Pure debugging function to show information about the current selection. */
const getBrowserSelection = (): string => {
  let selection = window.getSelection();

  if (selection === null) {
    return 'null';
  }

  if (selection === undefined) {
    return 'undefined';
  }

  var result = `\n  rangeCount: ${selection.rangeCount}`;

  if (selection.anchorNode == null) {
    result += '\n  anchorNode: null';
  } else {
    result += `\n  anchorNode: ${selection.anchorNode.nodeName}/${selection.anchorOffset}`;
  }

  if (selection.focusNode == null) {
    result += '\n  focusNode: null';
  } else {
    result += `\n  focusNode: ${selection.focusNode.nodeName}/${selection.focusOffset}`;
  }

  for (var i = 0; i < selection.rangeCount; i++) {
    var range = selection.getRangeAt(i);
    result += `\n  range ${i}:\n    startContainer: ${range.startContainer.nodeName}/${range.startOffset}\n    endContainer: ${range.endContainer.nodeName}/${range.endOffset}`;
  }

  return result;
};

/**
 * The text editor has the following components:
 * - An editor section (contentEditable) where the user edits the text.
 *   Paste operations are intercepted and redirected to the clipboardDiv.
 *   All changes are synchronized to a state.
 * - A state which contains the structure of the editor as a POJO.
 *   That state gets persisted together with the data.
 * - A hidden clipboardDiv where the pasted contents are pasted. It is then
 *   analyzed and its extracted content goes to the state, then to the text editor.
 */
export const TextEditor: React.FC = () => {
  const [state, setState] = React.useState(() => TestTextEditorState);

  var sectionRef = React.useRef<HTMLElement>(null);

  const beforeInputHandler = React.useCallback((event) => {
    const inputEvent = event as InputEvent;
    console.log('event', inputEvent.inputType, inputEvent.data, inputEvent);
    if (inputEvent.inputType === 'insertText') {
      setState((s) => textTyped(s, inputEvent.data || ''));
    }
    if (inputEvent.inputType === 'deleteContentBackward') {
      setState(deleteContentBackward);
    }
    if (inputEvent.inputType === 'deleteContentForward') {
      setState(deleteContentForward);
    }
    if (inputEvent.inputType === 'insertParagraph') {
      setState(insertParagraph);
    }
    event.preventDefault();
  }, []);

  const selectionChangeEventHandler = React.useCallback(() => {
    const selection = document.getSelection();
    setState((s) => ({ ...s, selection: computeSelection(sectionRef.current!, selection) }));
  }, []);

  React.useLayoutEffect(() => {
    var div = sectionRef.current!;
    div.addEventListener('beforeinput', beforeInputHandler);
    document.addEventListener('selectionchange', selectionChangeEventHandler);

    return () => {
      div.removeEventListener('beforeinput', beforeInputHandler);
      document.removeEventListener('selectionchange', selectionChangeEventHandler);
    };
  }, [beforeInputHandler, selectionChangeEventHandler]);

  React.useLayoutEffect(() => {
    // set the selection defined in the state.
    ensureStateSelection(sectionRef.current!, state);
  }, [state]);

  return (
    <div className={styles.wrapper}>
      <div>
        <section ref={sectionRef} contentEditable className={styles.textEditor} suppressContentEditableWarning={true}>
          {render(state)}
        </section>
        <pre>State selection: {state.selection === null ? '-' : `${JSON.stringify(state.selection, null, '  ')}`}</pre>
        <pre>Browser selection: {getBrowserSelection()}</pre>
      </div>
      <div>
        <pre>Blocks: {state.blocks === null ? '-' : `${JSON.stringify(state.blocks, null, '  ')}`}</pre>
      </div>
    </div>
  );
};
