import { Task, TaskState } from '../DataModel/Task';

export const getNewTask = (): Task => ({
  id: '',
  tagId: null,
  state: 'created',
  title: '',
  description: '',
  created: new Date(),
  modified: new Date(),
});

export const newState = (state: TaskState): Partial<Task> => {
  return {
    state,
    dateClosed: state === 'finished' || state === 'abandoned' ? new Date() : undefined,
  };
};
