import React from 'react';
import styles from './IconDesigner.module.scss';

export interface IIconPreviewProps {
  svg: string;
  size: number;
}

export const IconPreview: React.FunctionComponent<IIconPreviewProps> = ({ svg, size }) => {
  return (
    <div className={styles.iconPreview}>
      <div
        dangerouslySetInnerHTML={{ __html: svg }}
        style={{
          height: size,
          width: size,
        }}
      />
    </div>
  );
};
