import React from 'react';
import { IAuthManager } from './IAuthManager';
import { AuthManager } from './AuthManager';

const AuthManagerContext = React.createContext<IAuthManager>(undefined as any);

export interface IAuthManagerProviderProps {
  getNotAuthenticatedComponents: () => React.ReactElement | null;
  getAuthenticatedComponents: () => React.ReactElement | null;
}

export const AuthManagerProvider: React.FC<IAuthManagerProviderProps> = ({
  getNotAuthenticatedComponents,
  getAuthenticatedComponents,
}) => {
  const authManager = React.useMemo(() => new AuthManager(), []);
  const [authenticated, setAuthenticated] = React.useState(false);

  React.useEffect(() => {
    (async () => {
      await authManager.authenticate();
      setAuthenticated(true);
    })();
  }, [authManager]);

  return (
    <AuthManagerContext.Provider value={authManager}>
      {authenticated ? getAuthenticatedComponents() : getNotAuthenticatedComponents()}
    </AuthManagerContext.Provider>
  );
};

export const useAuthManager = () => {
  const authManager = React.useContext(AuthManagerContext);
  if (!authManager) {
    throw new Error('Please define an AuthManagerProvider upper in the component tree.');
  }
  return authManager;
};
