import React from 'react';
import { TaskState } from '../../../DataModel/Task';
import { Dialog } from '../../Utility/Dialog/Dialog';
import { TaskStateChoice } from './TaskStateChoice';

export type TaskStateEditionDialogProps = {
  /** The task to be rendered. */
  state: TaskState;
  /** Triggered when the task is being clicked. */
  onClose: (state?: TaskState) => void;
};

/** Edition of a task's state, displays all possible states. Calls onClose when a state is selected, or when "Cancel" is clicked. */
export const TaskStateEditionDialog: React.FunctionComponent<TaskStateEditionDialogProps> = ({ state, onClose }) => {
  return (
    <Dialog
      buttons={[
        {
          text: 'Annuler',
          onclick: () => {
            onClose();
          },
        },
      ]}
    >
      <TaskStateChoice state={state} onChange={onClose} />
    </Dialog>
  );
};
