import { IAuthManager } from './IAuthManager';
import { PublicClientApplication, AccountInfo } from '@azure/msal-browser';

const scopes = ['https://graph.microsoft.com/Files.ReadWrite'];

export class AuthManager implements IAuthManager {
  account: AccountInfo | undefined;

  readonly application = new PublicClientApplication({
    auth: {
      clientId: 'c1fef6c7-2dd3-425b-aa1e-e9c94ca00c63',
      authority: 'https://login.microsoftonline.com/consumers/',
    },
  });

  async authenticate() {
    await this.application.handleRedirectPromise();
    var accounts = this.application.getAllAccounts();

    if (accounts.length === 0) {
      // a login is needed. The page execution will stop here.
      await this.application.loginRedirect();
    }

    this.account = accounts[0];
  }

  async acquireToken() {
    await this.authenticate();

    var authResult = await this.application.acquireTokenSilent({
      scopes,
      account: this.account!,
    });
    return authResult.accessToken;
  }
}

export const getAuthManager = (): IAuthManager => {
  return new AuthManager();
};
