import React, { FunctionComponent } from 'react';
import styles from './Button.module.scss';
import { getClasses } from '../../../Helpers/cssHelper';

export interface ButtonProps {
  isLight?: boolean;
  isWide?: boolean;
  title?: string;
  onClick?: () => void;
}

export const Button: FunctionComponent<ButtonProps> = ({ isLight, isWide, title, onClick, children }) => {
  return (
    <div
      className={getClasses(styles.button, isLight && styles.light, isWide && styles.wide)}
      title={title}
      onClick={onClick}
    >
      {children}
    </div>
  );
};
