import { TaskState } from '../DataModel/Task';

export type Color = {
  /** red component, 0-255 */
  red: number;

  /** green component, 0-255 */
  green: number;

  /** blue component, 0-255 */
  blue: number;
};

export const getColor = (color: string): Color => {
  if (!color.match(/^#[0-9a-fA-F]{6}$/)) {
    throw new Error('invalid color');
  }

  return {
    red: parseInt(color.substr(1, 2), 16),
    green: parseInt(color.substr(3, 2), 16),
    blue: parseInt(color.substr(5, 2), 16),
  };
};

const getHexColor = (component: number): string => {
  if (component < 0) {
    component = 0;
  } else if (component > 255) {
    component = 255;
  }

  return Math.round(component).toString(16).padStart(2, '0');
};

export const getCssColor = (color: Color): string => {
  return `#${getHexColor(color.red)}${getHexColor(color.green)}${getHexColor(color.blue)}`;
};

export const mix = (color1: Color, color2: Color, coefficient: number = 0.5): Color => {
  return {
    red: color1.red * (1 - coefficient) + color2.red * coefficient,
    green: color1.green * (1 - coefficient) + color2.green * coefficient,
    blue: color1.blue * (1 - coefficient) + color2.blue * coefficient,
  };
};

/**
 * Computes a darker version of a color.
 * @param color The initial color.
 * @param amount How much darker the new color must be. Ranges from 0 (unchanged) to 1 (black).
 */
export const darker = (color: Color, amount: number): Color => {
  return {
    red: color.red * (1 - amount),
    green: color.green * (1 - amount),
    blue: color.blue * (1 - amount),
  };
};

/**
 * Computes a grayscale version of a color.
 * @param color The initial color.
 * @param amount How much gray the new color must be. Ranges from 0 (unchanged) to 1 (complete grayscale).
 */
export const greyscale = (color: Color, amount: number): Color => {
  var grey = 0.3 * color.red + 0.59 * color.green + 0.11 * color.blue;

  return {
    red: color.red * (1 - amount) + grey * amount,
    green: color.green * (1 - amount) + grey * amount,
    blue: color.blue * (1 - amount) + grey * amount,
  };
};

export const TaskColors: {
  [state in TaskState]: Color;
} = {
  created: getColor('#dadada'),
  'in-backlog': getColor('#ffa9f2'),
  next: getColor('#aa8afd'),
  active: getColor('#66dfff'),
  finished: getColor('#6eef00'),
  abandoned: getColor('#999999'),
};
