import React from 'react';
import { BoussoleData } from '../../DataModel/BoussoleData';
import { AutoSaverStatus } from './AutoSaverStatus';
import { useBoussoleDataStore } from '../BoussoleDataStore/BoussoleDataStoreProvider';
import { useBoussoleDataState } from '../BoussoleDataState/BoussoleDataStateProvider';

type AutoSaverState = {
  // the model being saved. Undefined if no model needs to be saved.
  modelBeingSaved?: BoussoleData;
  // A model queued to be saved, if a save operation is already ongoing (modelBeingSaved is thus not undefined), undefined otherwise.
  modelToSave?: BoussoleData;
};

export const useAutoSaver = (): AutoSaverStatus => {
  const boussoleDataStore = useBoussoleDataStore();
  const boussoleDataState = useBoussoleDataState();
  const state = React.useRef<AutoSaverState>({});
  const [saveStatus, setSaveStatus] = React.useState<AutoSaverStatus>('all-saved');

  const onModelChange = React.useCallback(
    (model: BoussoleData) => {
      if (state.current.modelBeingSaved) {
        // a save operation is already ongoing. Queue the model to be saved, overwriting an older version already queued (if any).
        state.current.modelToSave = model;
        return;
      }

      (async () => {
        state.current = { modelBeingSaved: model };
        setSaveStatus('saving');
        await boussoleDataStore.saveData(model);

        while (state.current.modelToSave) {
          state.current = { modelBeingSaved: model };
          await boussoleDataStore.saveData(model);
        }

        state.current = {};
        setSaveStatus('all-saved');
      })();
    },
    [boussoleDataStore]
  );

  React.useEffect(() => boussoleDataState.subscribe(onModelChange), [boussoleDataState, onModelChange]);
  return saveStatus;
};
