import React from 'react';
import styles from './Tasks.module.scss';
import { useBoussoleDataService } from '../../Services/BoussoleDataService/BoussoleDataServiceProvider';
import { newState } from '../../Helpers/tasksHelper';
import { Task } from '../../DataModel/Task';
import { TaskStateIndicator } from './TaskState/TaskStateIndicator';
import { TaskStateEditionDialog } from './TaskState/TaskStateEditionDialog';
import { TaskEditionDialog } from './TaskEditionDialog';

export type TaskComponentProps = {
  /** The task to be rendered. */
  task: Task;
};

/** Renders a single task in the list of tasks. */
export const TaskComponent: React.FunctionComponent<TaskComponentProps> = ({ task }) => {
  const { updateTask } = useBoussoleDataService();
  const [taskEditDialogOpen, setTaskEditDialogOpen] = React.useState(false);
  const [stateEditDialogOpen, setStateEditDialogOpen] = React.useState(false);

  return (
    <>
      <div onClick={() => setTaskEditDialogOpen(true)} className={styles.task}>
        <div className={styles.title}>{task.title}</div>
        <TaskStateIndicator state={task.state} onClick={() => setStateEditDialogOpen(true)} />
      </div>
      {taskEditDialogOpen && <TaskEditionDialog task={task} onClose={() => setTaskEditDialogOpen(false)} />}
      {stateEditDialogOpen && (
        <TaskStateEditionDialog
          state={task.state}
          onClose={(state) => {
            setStateEditDialogOpen(false);
            if (state) updateTask(task.id, newState(state));
          }}
        />
      )}
    </>
  );
};
